$border-settings: 2px solid black;
$c1: skyblue;
$c2: #D56EFF;
$c3: chartreuse;
$c4: yellow;
$c5: blue;
$c6: purple;
$cx: orange;
$gap: 5px;
$sebbe: 'https://www.onlygfx.com/wp-content/uploads/2019/02/7-noise-texture-overlays-1-768x589.png';
