        paint-order: stroke; }

    .X, .C1, .C2, .C3, .C4, .C5, .C6 {
        stroke: black;
        stroke-width: 4px;
        paint-order: stroke; } }

rect.homebag {
    fill: #50C878 !important;
 }    // stroke: red !important
text.homebag {
    tspan {
        // fill: black
        stroke: black;
        stroke-width: 4px;
        paint-order: stroke; } }

.to_the_depths {
    visibility: hidden !important;
    opacity: 0.5;
    transform: translateY(-10%); }
.blur {
    filter: blur(4px) !important; }

.w1-3 {
    width: 33.333333333% !important; }
.w2-3 {
    width: 66.666666666% !important; }
.w1-2 {
    width: 50% !important; }

