.animated-page {
    position: absolute; }
.main {
    position: relative; }

.main, .animated-page {
    width: 100%;
    flex: 1;
    // background-color: #303030
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;

    .sidebar {
        font-size: 13px;
        position: relative;

        width: 25%;
        height: 100%;
        // padding: 20px 0px 20px 10px
        // border-right: $border-settings
        overflow-x: hide;
        // overflow-y: scroll
        padding: $gap;
        padding-right: 0;
        padding-left: 0;
        .sidebar_bubble {
            height: 100%;
            width: 100%;
            padding: 20px;
            background-color: rgba(240,255,255,.9);
            border: $border-settings;
            border-radius: 7px;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);

            > *:first-child {
                margin-top: 0; } } }
    .content {
        // padding: 20px
        width: 80%;
        height: 100%;
        background-color: #151515;
        // background-color: black
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        padding: $gap;
        gap: $gap;

        .cy {
            border: $border-settings;
            border-radius: 7px;
            // background-image: linear-gradient(200deg,#252525 0%,rgba(25, 25, 25, 0.95) 10%,rgba(25, 25, 25, 0.9) 20%,rgba(25, 25, 25, 0.85) 30%,rgba(25, 25, 25, 0.8) 40%,rgba(25, 25, 25, 0.75) 50%,rgba(25, 25, 25, 0.7) 60%,rgba(25, 25, 25, 0.65) 70%,rgba(25, 25, 25, 0.6) 80%,rgba(25, 25, 25, 0.55) 90%,#252525 100%)
            background-color: #252525;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3); }

        .horizontal-split {
            height: 100%;
            width: 100%;
            display: flex;
            gap: $gap;
            flex-direction: column;
            position: relative; }

        .cc {
            width: 100%;
            height: 100%;
            background-color: #252525;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            gap: $gap;
            border: $border-settings;
            border-width: 2px;
            border-radius: 7px;
            border-color: $cx;
            gap: 0;
            background-color: #252525;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
            .cy {
                background-color: transparent;
                box-shadow: none;
                border: none; }
            >*:last-child {
                border: none !important; }

            // > *
            //     border-bottom: $border-settings
            // > *:last-child
 }            //     border-bottom: none

        .svg_popup {
            position: absolute;
            border: $border-settings;
            border-radius: 7px;
            width: 80%;
            left: 10%;
            height: 80%;
            top: 10%;
            background-color: #252525;
            z-index: 2;
            box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
            // transition: filter 0.2s ease-in-out
            transition: 0.2s ease-in-out; } } }




.svg_container {
    width: 100%;
    height: 100%;
    position: relative;
    transition: filter 0.2s ease-in-out;
    .cy {
        width: 100%;
        height: 100%; }
    .svg_label {
        position: absolute;
        top: 7px;
        left: 7px;
        color: white;
        background-color: #101010;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 14px;
        border: $border-settings;
        font-weight: 300; }

    .svg_counter {
        position: absolute;
        top: 45px;
        left: 5px;
        color: white;
        background-color: #101010;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 18px;
        border: 2px solid black;
        &.valid {
            border-color: chartreuse; }
        &.invalid {
            border-color: black; } }

    &.interactive {
        circle, rect {
            cursor: pointer; }
        .cy {
            border-color: $cx;
            border-width: 2px; } }
    &.active {
        // border-right: 3px solid green !important
        // border-left: 3px solid green !important
        // border-bottom: 1px solid green !important
        background-color: #151815;
        position: relative;
        background-color: transparent;
        overflow: hidden;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-size: cover;
            // background-image: url($sebbe)
            animation: sebbe 30s infinite;
 }            // z-index: 0
        .cy {
            position: relative;
            z-index: 0; } }
    &.full svg {
        position: absolute;
        pointer-events: none;
        height: 100%;
        width: 300%;
        // background-color: rgba(255,0,0,0.1)
        // border: 2px solid red
        z-index: 1;
        circle {
            pointer-events: auto; } }

    &.full.top svg {
        top: 0;
        left: 0; }
    &.full.mid svg {
        top: 0;
        left: -100%; }
    &.full.bot svg {
        top: 0;
        left: -200%; }
    &.full {
        border-right: 2px dotted #050505; } }

@keyframes sebbe {
    0% {
        transform: scale(1); }
    50% {
        transform: scale(1.5); }
    100% {
        transform: scale(1); } }

